export default {
	data() {
		return {
			imgUrl: this.$imgUrl,
			current: 0,
			bannerList: [],
			dataFormList: []
		};
	},
	mounted() {
		this.getInternationalBanner()
		this.getInternational()
	},
	methods:{
		// 获取banner
		getInternationalBanner () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('Visitor/get_international_banner').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.bannerList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			})
		},
		// 获取列表
		getInternational () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('Visitor/get_international').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.dataFormList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			})
		},
		change (e) {
			this.current = e.detail.current;
		},
		skipClick(item) {
			var items = JSON.stringify(item)
			uni.navigateTo({
				url: '../../pagesKnowUs/KnowUsInternationalNewss/KnowUsInternationalNewss?item=' + items
			})
		}
	}
}
